.Nav {
  position: fixed;
  bottom: 0;
  left: 0;
  height: 55px;
  width: 55px;
  background-color: rgba(0, 0, 0, 0.432);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.3s ease;
  backdrop-filter: blur(3px);
  &__box-expand {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__list {
    list-style-type: none;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    &--expand {
      justify-content: space-evenly;
    }
  }
  &__expand {
    display: flex;
    align-items: center;
  }
  &__menu-icons {
    display: none;
    color: white;
    -webkit-tap-highlight-color: transparent;
    &--expand {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 4px;
      min-width: 50px;
      height: 100%;
      color:white;
    }
  }
  &__icons-name {
    margin-top: 2px;
  }
  &__backIcon {
    width: 25px;
    height: 25px;
    position: relative;
    &--bars {
      width: 14px;
      height: 2px;
      background-color: #fff;
      position: absolute;
      top: 50%;
      left: 0;
      border-radius: 2px;
      transform: translateY(-50%);
      &::before {
        content: '';
        width: 25px;
        height: 2px;
        background-color: #fff;
        position: absolute;
        top: -10px;
        left: 0;
        border-radius: 2px;
      }
      &::after {
        content: '';
        width: 25px;
        height: 2px;
        background-color: #fff;
        position: absolute;
        top: 10px;
        left: 0;
        border-radius: 2px;
      }
    }
  }
  &__icons {
    transition: all 1s ease;
  } 
  &__user-icon {
    &--foodPlan,
    &--home,
    &--food,
    &--user {
      fill: white;
    }
    &--foodPlan {
      width: 22px;
    }
    &--home {
      width: 33px;
    }
    &--food {
      width: 30px;
    }
    &--user {
      width: 23px;
    }
  }
  &__expand {
    transition: all 0.3s ease;
    &--rotate .Nav__backIcon--bars {
      width: 25px;
    }
    &--rotate .Nav__backIcon--bars::before {
      width: 15px;
      left: 100%;
      transform: translateX(-100%);
    }
    &--rotate .Nav__backIcon--bars::after {
      width: 15px;
      left: 100%;
      transform: translateX(-100%);
    }
  }
}

.active {
  color: $main-color;
  .Nav__icons  {
    fill: $main-color;
  }
}