.MealItem {
  width: 95vw;
  height: 220px;
  color: white;
  margin-bottom: 20px;
  border-radius: 8px;
  position: relative;
  background-color: rgba(255, 255, 255, 0.466);
  box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.25);
  visibility: hidden;
  overflow: hidden;
  &__imageWrapper {
    width: 100%;
    height: 75%;
    overflow: hidden;
  }
  &__image {
    width: 100%;
  }
  &__name {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -100%);
    width: 90%;
    font-size: 1.8rem;
    text-shadow: -3px 0px 5px rgba(0, 0, 0, 1);
    text-align: center;
  }
  &__favourite {
    width: 45px;
    position: absolute;
    top: 5%;
    left: 5%;
  }
  &__clockIcon {
      width: 25px;
      position: absolute;
      bottom:10px;
      right: 10px;
  }
}
